<template>
  <div class="accounting-subject">
    <div class="subjects">
      <swiper class="swiper" :options="swiperOption" ref="swiper" :onSlideNextEnd="onSlideNextEnd">
        <!-- slides -->
        <swiper-slide v-for="(el,index) in randomSubjects" :key="index">
          <h2>{{el.name}}</h2>
          <p v-html="el.desc"></p>
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
    <div class="buttons">
      <button :class="key===currentParent ? 'pushy__btn--red': 'pushy__btn--green'" @click="picker(key)" v-for="(el,key, index) in subjects" :key="index" class="pushy__btn pushy__btn--df">{{key}}</button>
    </div>
  </div>
</template>
<script>
import 'swiper/dist/css/swiper.css'

import { swiper, swiperSlide } from 'vue-awesome-swiper'

export default {
  components: {
    swiper,
    swiperSlide
  },
  data () {
    return {
      subjects: {
        资产: {
          库存现金: '',
          银行存款: '',
          其他货币资金:
            '是核算银行汇票存款、银行本票存款、信用卡存款、信用证等其它货币资金的科目。',
          交易性金融资产: '企业为交易目的所持有的债券投资、股票投资、基金投资等交易性金融资产的公允价值',
          应收票据: '业因销售商品、提供劳务等而收到的商业汇票，包括银行承兑汇票和商业承兑汇票。',
          应收账款: '企业因销售商品、提供劳务等经营活动应收取的款项',
          其它应收款:
            '企业除存出保证金、买入返售金融资产、应收票据、应收账款、预付账款、应收股利、应收利息、应收代位追偿款、应收分保账款、应收分保合同准备金、长期应收款等以外的其他各种应收及暂付款项。',
          坏账准备: '是应收账款的备抵账户。',
          预付账款: '',
          在途货资: '',
          原材料: '',
          库存商品: '',
          长期股权投资: '是核算企业对外进行的股权性质的投资，期限在一年以上。',
          固定资产: '',
          累计折旧:
            '是核算企业固定资产的磨损（消耗）价值（会计上称之为折旧）是固定资产的备抵账户。<br>（固定资产－累计折旧＝固定资产净值）',
          在建工程: '',
          固定资产清理: '',
          无形资产: '',
          累计摊销: '是无形资产的备抵账户，是核算无形资产的摊销账户。'
        },
        负债: {
          短期借款: '',
          应付票据: '',
          应付账款: '',
          其它应付账款:
            '是核算企业除应付账款，应付票据以外其他应付暂收款项。例如：存入保证金(押金)、应付的租金等。更多会计资料请加用户名了解。',
          应付职工薪酬: '',
          应交税费: '',
          应付股利: '',
          应付利息: ''
        },
        所有者权益: {
          实收资本:
            '是核算企业的股东投入的资本金账户，按公司规定，投入资本可以是货币、事物和无形资产等，企业不得随意增减，如需增减，须经工商部门批准。',
          资本公积:
            '核算企业因资本而产生的，包括资本溢、折价、资本汇兑差等，此科目可以转增资本金。',
          盈余公积:
            '是核算企业按照公司法规定，用净利润提取的留待企业今后发展的基金。',
          本年利润: '',
          利润分配: ''
        },
        成本类: {
          生产成本:
            '是核算企业生产过程中过程成本核算的科目，生产成本包括直接材料，直接人工和间接制造成本。按“基本生产成本”、“辅助生产成本”设置二级明细科目。',
          制造费用:
            '是核算生产车间发生的，发生时不能直接归属于某个产品对象的间接制造成本，是产品成本中的间接制造成本，月末该科目余额为零（转入生产成本）。',
          劳务成本: ''
        },
        损益类: {
          主营业务收入: '',
          主营业务成本: '',
          其它业务收入:
            '是核算企业因其他业务而产生的收入。<br>如：工业企业的出售原材料、对外出租和转让无形资产使用权等取得的收入。',
          营业外收入: '包括：处置非流动资产的利得，受赠利得和盘盈利得等。',
          营业外支出:
            '包括捐赠支出、非常损失、罚款支出、处置非流动资产损失等。',
          营业税金及附加:
            '是核算企业因主营业务而产生的各种税金，包括营业税、消费税、城市维护建设税和教育费附加等费用类税金的科目。（不含增值税）。',
          销售费用:
            '包括运输费、包装费、广告费、展览费和专设销售机构的销售人员工资等。',
          管理费用:
            '包括运输费、包装费、广告费、展览费和专设销售机构的销售人员工资等。',
          财务费用:
            '是核算企业为筹集生产经营所需资金而与银行或金融机构产生的借款费用。<br>包括：利息支出和手续费用等。',
          资产减值损失:
            '是核算企业资产发生跌价或减值产生的损失，包括坏账损失、存货跌价损失和固定资产减值损失等等。',
          所得税费用: ''
        }
      },
      randomSubjects: [],
      swiperOption: {
        effect: 'coverflow',
        grabCursor: true,
        centeredSlides: true,
        slidesPerView: 'auto',
        coverflowEffect: {
          rotate: 50,
          stretch: 0,
          depth: 100,
          modifier: 1,
          slideShadows: true
        },
        pagination: '.swiper-pagination',
        onSlideChangeEnd: swiper => {
          this.currentParent = this.randomSubjects[swiper.realIndex].parent
        }
      },
      activeIndex: -1,
      currentParent: ''
    }
  },
  methods: {
    setRandomSubjects () {
      let result = []
      for (let k in this.subjects) {
        for (let key in this.subjects[k]) {
          result.push({
            name: key,
            desc: this.subjects[k][key],
            parent: k
          })
        }
      }
      this.randomSubjects = [...result.sort(() => {
        return 0.5 - Math.random()
      })]
    },
    picker (name) {
      var swiper = this.$refs.swiper.swiper
      var curName = this.randomSubjects[swiper.activeIndex].name
      if (this.subjects[name][curName] === undefined) {
        this.activeIndex = swiper.activeIndex
        this.$toast.top('error')
      } else {
        swiper.slideNext()
        this.activeIndex = -1
      }
    },
    onSlideNextEnd (e) {

    }
  },
  mounted () {
    this.setRandomSubjects()
  }
}
</script>
<style lang="less">
.accounting-subject {
  position: absolute;
  background-color: #ccc;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  .subjects {
    flex: 1;
    display: flex;
    flex-direction: column;
  }
  .buttons {
    margin: 0 10px;
    button {
      width: 100%;
      margin-bottom: 10px;
    }
  }
}
.pushy__btn {
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  display: inline-block;
  border: none;
  border-radius: 0.3em;
  -webkit-box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.1),
    inset 0 -0.25em 0 rgba(0, 0, 0, 0.25), 0 0.25em 0.25em rgba(0, 0, 0, 0.05);
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.1),
    inset 0 -0.25em 0 rgba(0, 0, 0, 0.25), 0 0.25em 0.25em rgba(0, 0, 0, 0.05);
  color: #fff;
  font-weight: 400;
  line-height: 1.5;
  padding: 0.5em 1.5em 0.75em;
  position: relative;
  vertical-align: middle;
  user-select: none;
  transition: all .3s;
}
.pushy__btn:active {
  -webkit-box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.2),
    inset 0 2px 0 rgba(255, 255, 255, 0.1),
    inset 0 0.25em 0.5em rgba(0, 0, 0, 0.05);
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.2),
    inset 0 2px 0 rgba(255, 255, 255, 0.1),
    inset 0 0.25em 0.5em rgba(0, 0, 0, 0.05);
  margin-top: 0.25em;
  padding-bottom: 0.5em;
}
.pushy__btn:active,
.pushy__btn:focus {
  outline: none;
}
.pushy__btn--blue {
  background-color: #1199ee;
}
.pushy__btn--green {
  background-color: #10ac84;
}
.pushy__btn--red {
  background-color: #ee4444;
}
.pushy__btn--lg {
  font-size: 1.5em;
}
.pushy__btn--df {
  font-size: 1.25em;
}
.pushy__btn--md {
  font-size: 1em;
}
.pushy__btn--sm {
  font-size: 0.75em;
}
.swiper{
  flex:1;
  width: 100%;
}
.swiper-slide{
  width: 70%;
  background: #fff;
  h2{
    text-align: center;
    font-size: 28px;
    padding-top: 20%;
    color: #10ac84;
  }
  p{
    padding: 20px;
  }
}
.swiper-error{
  background-color: #ffd9d9;
}
</style>



