import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import AccountSubject from '@/components/accountingSubject'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: AccountSubject
    },
    {
      path: '/accountingSubject',
      component: AccountSubject
    }
  ]
})



